import React, { useEffect, useState, useRef, useForm } from 'react';
import service from 'Src/utils/request';
// import AddAndEditBlog from './components/AddAndEditBlog';
import { Button, Tooltip, Select, Form, DatePicker, Input, Table, Tag, Spin, Space, Popconfirm } from 'antd';
// import ReadOnlyMarkdown from 'Src/components/ReadOnlyMarkdown';
import Panel from 'Src/components/Panel';

import moment from 'moment';

import styles from './index.css';

const { Option, } = Select;
const tagList = [
  {
    label: '',
    value: '',
  },
  {
    label: '',
    value: '',
  },
  {
    label: 'UI',
    value: 'UI',
  },
];
const typeList = [
  {
    label: '',
    value: '',
  },
  {
    label: '',
    value: '',
  },
  {
    label: '',
    value: '',
  },
];

const DraftBlog = (props) => {
  const { history, } = props;
  const [form] = Form.useForm();

  const [queryLoading, set_queryLoading] = useState(false);

  const [visibleForEditDrawer, set_visibleForEditDrawer] = useState(false);
  const [blogList, set_blogList] = useState([]);

  const columns = [
    {
      title: '',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '',
      dataIndex: 'username',
      key: 'username',
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      render: text => text ? moment(text).format('YYYY-MM-DD HH:mm:ss') : '-',
    },
    {
      title: '',
      dataIndex: 'type',
      key: 'type',
      render: text => text ? text.split(',').map(item => (<Tag key={item}>{item}</Tag>)) : '-',
    },
    {
      title: '',
      dataIndex: 'tags',
      render: text => text ? text.split(',').map(item => (<Tag key={item}>{item}</Tag>)) : '-',
    },
    {
      title: '',
      dataIndex: 'objectId',
      width:140,
      // eslint-disable-next-line react/display-name
      render: (text, { objectId, username }) => (
        <Space >
          {
            window.user?.username === username ?
              <Popconfirm
                title=''
                okText=''
                cancelText=''
                onConfirm={() => deleteBlogFn(objectId)}
              >
                <span className='a-ele' ></span>
              </Popconfirm> :
              <Tooltip title="">
                <span className='disable-ele' ></span>
              </Tooltip>
          }
          {
            window.user?.username === username ?
              <a
                className='a-ele'
                target='_blank'
                rel='noopener noreferrer'
                href={`/draftBlog?objectId=${text}`}
              ></a> :
              <Tooltip title="">
                <span className='disable-ele' ></span>
              </Tooltip>
          }

          <a className='a-ele' target='_blank' rel='noopener noreferrer' href={`/blogDetail?objectId=${text}`} ></a>
        </Space>
      ),
    },
  ];

  useEffect(() => {
    getBlogListFn()
  }, [])

  const getBlogListFn = (params = {}) => {
    set_queryLoading(true)
    const query = window.Bmob.Query('blogs');
    query.order('-createdAt');
    query.find().then(res => {
      set_blogList(res);
      console.log(118, res)
      setTimeout(() => {
        set_queryLoading(false)
      }, 200)
    });
  }
  const deleteBlogFn = (objectId) => {
    set_queryLoading(true)
    const query = window.Bmob.Query('blogs');
    query.destroy(objectId).then(res => {
      getBlogListFn()
    }).catch(err => {
      console.log(err)
    })
  }

  // const openvisibleForEditDrawer = () => {
  //   // window.user;
  //   if (window.user) {
  //     set_visibleForEditDrawer(true)
  //   } else {
  //     history?.push('/login')
  //   }
  //   console.log(111, window.user)

  //   //
  // }


  const onFinish = values => {
    console.log(values);
    getBlogListFn()
  };
  const onReset = () => {
    form.resetFields();
    getBlogListFn()
  };
  return (
    <Panel
    // PanelTopLeftEle={<Button onClick={openvisibleForEditDrawer}>btn</Button>}
    >
      <Spin spinning={queryLoading}>

        <Form
          className={styles.form_Wrap}
          form={form}
          name='control-hooks'
          onFinish={onFinish}
          initialValues={{ mouthtime: moment('2015/01', 'YYYY/MM'), }}
        >
          <Form.Item
            name='mouthtime'
            label=''
          >
            <DatePicker format={'YYYY/MM'} picker='month' />
          </Form.Item>
          <Form.Item name='tags' label='' >
            <Select mode='tags' tokenSeparators={[',']} placeholder=''>
              {
                tagList.map(item => (<Option key={item.value} value={item.value}>{item.label}</Option>))
              }
            </Select>
          </Form.Item>
          <Form.Item name='type' label='' >
            <Select mode='tags' tokenSeparators={[',']} placeholder=''>
              {
                typeList.map(item => (<Option key={item.value} value={item.value}>{item.label}</Option>))
              }
            </Select>
          </Form.Item>
          <Form.Item
            name='content'
            label=''
          >
            <Input placeholder='' />
          </Form.Item>
          <Form.Item
            className={styles.form_item_btns}
          >
            <Button type='primary' htmlType='submit'></Button>
            <Button htmlType='button' onClick={onReset} className={styles.reset_btn}></Button>
          </Form.Item>
        </Form>

        <Table
          dataSource={blogList}
          columns={columns}
          rowKey='objectId'
        />
      </Spin>
    </Panel>
  )
}

export default DraftBlog
